import React, {useState, useEffect} from 'react'
import User from './User'
import styles from './Users.module.css'
import Filters from '../filters/Filters'
import userPage from './userPage'
import { Route } from 'react-router-dom'
import { NavLink } from 'react-router-dom'

function Users() {

  const convertTime = time => {
      const zero = num => {
          if (num < 10) {
              return '0' + num
          } else {
              return
          }
      }

      const date = new Date(time * 1000)
      const month = zero(date.getMonth())
      const day = zero(date.getDay())
      const year = date.getFullYear()

      return `${day}.${month}.${year}`
  }  

  const usersUrl = 'https://api.stackexchange.com/2.2/users?order=desc&sort=reputation&site=stackoverflow&filter=!9_bDDp)d5&key=Vv7UNHs4lXzS9vMjnF5JEQ(('
  const [item, setItem] = useState(null)
  const [tags, setTags] = useState([])
  
  useEffect(() => {
    fetch(usersUrl)
      .then(response => response.json())
      .then(data => {
        setTags(data.items)

        console.log(data.items)

        setItem(data.items.map(el => <User userPageSrc={<NavLink to='/user'>Main</NavLink>} source={el.profile_image} name={el.display_name} location={el.location} reputation={el.reputation} tags={el.user_id} badgeBronze={el.badge_counts.bronze} badgeSilver={el.badge_counts.silver} badgeGold={el.badge_counts.gold} about={el.about_me} />))
      })
  }, [usersUrl])

  return (
    <div>
      <Filters sortOne='Reputation' sortTwo='Creation Date' sortThree='Names' />
      <div className={styles.usersWrapper}>{item}</div>
    </div>
  )

}

export default Users