import React, {useState, useEffect} from 'react'
import Filters from '../filters/Filters'
import Tag from './Tag'
import styles from './Tags.module.css'
import {BrowserRouter, Route} from 'react-router-dom'

function Tags() {
    const tagsUrl = 'https://api.stackexchange.com/2.2/tags?order=desc&sort=popular&site=stackoverflow&key=Vv7UNHs4lXzS9vMjnF5JEQ(('
    const [tag, setTag] = useState(null)

    useEffect(() => {
        fetch(tagsUrl)
            .then(response => response.json())
            .then(data => {
                // console.log(data.items)
                setTag(data.items.map(el => <Tag name={el.name} amount={el.count} />))
            })
    }, [tagsUrl])

    return (
        <div>
            <Filters sortOne='Popularity' sortTwo='Activity' sortThree='Name' />
            <div className={styles.wrapper}>
                {tag}
            </div>
        </div>
    )
}

export default Tags

