import React from 'react'
import styles from './Question.module.css'

function Question(props) {
  return (
    <div className={styles.item}>
      <div className={styles.rates}>
        <div className={styles.votes}>
          <div>{props.votes}</div>
          <p>votes</p>
        </div>
        <div className={styles.answers}>
          <div>{props.answers}</div>
          <p>answers</p>
        </div>
        <div className={styles.views}>
          <div>{props.views}</div>
          <p>views</p>
        </div>
        <div className={styles.answered}>
          <p>answered:</p>
          <div>{props.answer}</div>
        </div>
      </div>
      <div className={styles.desc}>
        <a href='#' className={styles.text} target='_blank'>{props.title}</a>
        <div className={styles.tags}>
          <p>tags:</p>
          <div className={styles.tagsNames} id={props.tagsList}>
            {props.tags.map(t => <a href='#' className={styles.tag} target='_blank'>{t}</a>)}
          </div>
        </div>
        <div className={styles.ownersInfo}>
          <div className={styles.name}>
            <p>name:</p>
            <div><a href={props.userLink} className={styles.userName} target='_blank'>{props.name}</a></div>
          </div>
          <div className={styles.date}>
            <p>date:</p>
            <div>{props.date}</div>
          </div>
          <div className={styles.reputation}>
            <p>reputation:</p>
            <div>{props.reputation}</div>
          </div>
        </div>
      </div>      
    </div>
  );
}

export default Question;
