import React from 'react'
import logo from './logo.png'
import styles from './Header.module.css'
import { NavLink } from 'react-router-dom'

function Header() {
    return(
        <div className={styles.header}>
           <div className={styles.logo}>
                <img className={styles.logoImg} src={logo} />
                <span className={styles.logoName}>stack<span className={styles.boldName}>overflow</span></span>
           </div>
            <ul className={styles.menu}>
                <li><NavLink to='/main' activeClassName={styles.active}>Main</NavLink></li>
                <li><NavLink to='/users' activeClassName={styles.active}>Users</NavLink></li>
                <li><NavLink to='/tags' activeClassName={styles.active}>Tags</NavLink></li>
            </ul>
            <div className={styles.wrapper}>
                <input type='text' id={styles.search} placeholder='what are you looking for'></input>
                <button className={styles.search}>Search</button>
            </div>
            <button className={styles.login}>Log in</button>
        </div>
    )
}

export default Header