import React, {useState, useEffect} from 'react'
import styles from './Tag.module.css'

function Tag(props) {


    const encodeName = (name) => {

        let arr = name.split('')

        if (arr.some(el => el === '#')) {
            arr[1] = '%23'
            console.log(arr)
            return arr.join('')
        } else {
            return arr.join('')
        }

    }

    const wikiUrl = `https://api.stackexchange.com/2.2/tags/${encodeName(props.name)}/wikis?site=stackoverflow&key=Vv7UNHs4lXzS9vMjnF5JEQ((`
    const [wiki, setWiki] = useState(null)

    console.log(wikiUrl)

    useEffect(() => {
        fetch(wikiUrl)
            .then(response => response.json())
            .then(data => {
                console.log('yo')
                // setWiki(data.items.excerpt)

                setWiki(data.items.map(el => el.excerpt))                
            })
    }, [wikiUrl])

    return(
        <div className={styles.tagInfo}>
            <a href={props.questions} target='_blank' className={styles.link}>{props.name}</a>
            <p className={styles.wiki}>{wiki}</p>
            <p className={styles.amount}>questions: {props.amount}</p>
        </div>
    )
}

export default Tag