import React, {useState, useEffect} from 'react'
import styles from './User.module.css'

function User(props) {

  const favTagsUrl = `https://api.stackexchange.com/2.2/users/${props.tags}/top-tags?site=stackoverflow&key=Vv7UNHs4lXzS9vMjnF5JEQ((`
  const [favTags, setFavTags] = useState([])

  useEffect(() => {
      fetch(favTagsUrl)
          .then(response => response.json())
          .then(data => {
            const arr = data.items
            const arrTags = arr.slice(0, 3)
            
            setFavTags(arrTags)
          })
  }, [favTagsUrl])

  return (
    <div className={styles.user}>
      <a href={props.userPageSrc} className={styles.avatar}><img src={props.source} /></a>
      <div className={styles.info}>
        <a href={props.userPageSrc} target='_blank' className={styles.name}>{props.name}</a>
        <p className={styles.location}>{props.location}</p>
        <p className={styles.reputation}>{props.reputation}</p> 
        <div className={styles.tags}>
         {favTags.map(el => <a href='#' target='_blank' className={styles.tag}>{el.tag_name}</a>)}
        </div>
        <div className={styles.aboutUser}>
          <p>bronze: {props.badgeBronze}</p>
          <p>silver: {props.badgeSilver}</p>
          <p>gold: {props.badgeGold}</p>
          <p className={styles.about} dangerouslySetInnerHTML={{__html: props.about}}></p>
        </div>
      </div>  
    </div>
  )
}

export default User;
