import React, {useState, useEffect} from 'react'
import Filters from '../filters/Filters'
import Question from './Question'
import {BrowserRouter, Route} from 'react-router-dom'
import userPage from '../users/userPage'

function QuestionList() {

  const convertTime = time => {
    const zero = num => {
        if (num < 10) {
            return '0' + num
        } else {
            return
        }
    }

    const date = new Date(time * 1000)
    const month = zero(date.getMonth())
    const day = zero(date.getDay())
    const year = date.getFullYear()
    const hour = date.getHours()
    const min = date.getMinutes()

    return `${day}.${month}.${year} at ${hour < 10 ? '0' + hour : hour}:${min < 10 ? '0' + min : min}`
  }
  
  const answer = result => {
    if (result === true) {
      return 'yes'
    } else {
      return 'no'
    }
  }

  let url = 'https://api.stackexchange.com/2.2/questions?order=desc&site=stackoverflow&key=Vv7UNHs4lXzS9vMjnF5JEQ(('
  const [item, setItem] = useState(null)
  const [Questions, setQuestions] = useState([])

  useEffect(() => {
    fetch(url)
      .then(response => {
        return response.json()
      })
      .then(data => {
        setQuestions(data.items)   
        console.log(data)    

        setItem(data.items.map(el => <Question title={el.title} tags={el.tags} name={el.owner.display_name} userLink={<Route path='/user' component={<userPage />} />} reputation={el.owner.reputation} date={convertTime(el.creation_date)} votes={el.score} answers={el.answer_count} views={el.view_count} answer={answer(el.is_answered)} />))
      })
  }, [url])

  const votesSort = () => {

    let votesArr = item.map(el => el.props.votes)
    console.log(votesArr)
    const sorted = votesArr.sort((a, b) => a - b)
    
    console.log(sorted)
  }

  return (
    <div>
      <Filters sortOne='Activity' sortTwo='Votes' sortThree='Creation' votesSort={votesSort}/>
      <div>{item}</div>
    </div>
  )

}

export default QuestionList
